:root {
  --bg-color: white;
  --color: black;

  font-size: 18px;
}

[data-theme="dark"] {
  --bg-color: #333;
  --color: white;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  background-color: var(--bg-color);
  color: var(--color);
}

.pill {
  border-radius: 20%/50%;
}

.fade-in {
  opacity: 1;
}